:root {
  --fontsize: 25px;
  --width: 90%;
  --maincolor: rgb(78, 193, 168);
  --footercolor: rgba(0, 0, 0, 0.747);
}
* {
  box-sizing: border-box;
  /* border: 1px solid black; */
}
#author {
  width: var(--width);
  font-style: italic;
  display: block;
  margin: auto;
  margin-top: 10px;
  text-align: end;
  font-size: calc(var(--fontsize) * 0.7);
  color: rgba(99, 98, 98, 0.791);
  font-family: "Gill Sans MT";
}
.click:hover {
  cursor: pointer;
  opacity: 0.7;
}
.iconq {
  height: calc(var(--fontsize) * 1.2);
  color: black;
}
#text {
  width: var(--width);
  line-height: var(--fontsize);
  margin: 20px auto 0 auto;
  font-size: var(--fontsize);
  padding: 5px;
  padding-bottom: 10px;
  text-align: center;
  color: var(--maincolor);
  transition: color 1s ease-in;
}
#text:active {
  animation: txt3 1s ease-in;
}
@keyframes txt3 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.clickables {
  width: var(--width);
  margin: 30px auto 20px auto;
  display: flex;
  justify-content: space-between;
  height: 45px;
}
#new-quote {
  font-size: 110%;
  background-color: var(--maincolor);
  border-radius: 5px;
  color: var(--box-color);
  border: none;
  transition: opacity 0.2s ease-in-out, background-color 1s ease-in;
}
.icon2 {
  height: 100%;
  color: var(--maincolor);
  transition: opacity 0.2s ease-in-out, color 1s ease-in;
  display: inline;
  margin-right: 3vw;
}
.icon3 {
  height: 25px;
  margin: 0 2.5px 0 2.5px;
  color: var(--footercolor);
  transition: color 0.2s ease-in-out;
}
.links {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.icon3:hover {
  color: white;
}
i {
  padding: 0 5px 0 5px;
  color: var(--footercolor);
  margin-right: 5px;
}
#fclass {
  display: flex;
  align-items: center;
  color: var(--footercolor);
}
a {
  text-decoration: none;
}
#fclass > a {
  margin-right: 5px;
}
