:root {
  --box-color: rgb(234, 229, 229);
}
body {
  margin: 0;
  height: 100vh;
  width: 100vw;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background-color: var(--maincolor);
  transition: background-color 1.2s ease-in;
}
#root {
  margin: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
}

#quote-box {
  width: 500px;
  position: relative;
  top: -10vh;
  background-color: var(--box-color);
  border-radius: 10px;
}

#footer {
  margin-top: -3vh;
  color: blue;
  height: 6vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
@media only screen and (max-width: 500px) {
  #quote-box {
    width: 300px;
  }
}
